<template>
  <div>
    <div class="plat-header">
      <div>
        <el-button @click="showAddDialog" class="addBtn" icon="my-xinzengziyuan">
          新增资源
        </el-button>
      </div>
      <el-form ref="form" :model="form" :inline="true" class="source-div-form">
        <el-form-item label="资源名称">
          <el-input v-model="form.formdata.name" class="area-input-search">
          </el-input>
        </el-form-item>
        <el-form-item label="访问地址">
          <el-input v-model="form.formdata.uri">

          </el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="form.formdata.remark">

          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="requestResource" type="primary"> 搜索</el-button>
        </el-form-item>

      </el-form>
    </div>
    <!-- 上部命令行 -->

    <!-- table表格 -->
    <da-table :colums="columnData" ref="table" :search-data="form" @callBack="requestResource" :showSelection="false">
      <el-table-column property="operation" label="操作" width="60" slot="operation" align="center">
        <template slot-scope="scope">
          <el-button size="small" @click="edit(scope.row)" type="warning">
            编辑
          </el-button>
          <!--<el-button @click="deleteSource(scope.row)" type="danger" size="small">-->
          <!--删除-->
          <!--</el-button>-->
        </template>
      </el-table-column>
    </da-table>

    <da-dialog :title="title" :visible.sync="addSourceVisible" class="source-div-addDialog2" @close="closeDialog('source_add2')" @open="openDialog">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="source_add2">

        <el-form-item label="资源名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="访问地址" prop="uri">
          <el-input auto-complete="off" v-model="addForm.uri" @change="urlChange(addForm.uri,'uri')" @keydown.native="keydown($event)"></el-input>
        </el-form-item>
        <el-form-item label="请求类型" prop="method">
          <el-radio-group v-model="addForm.method" @change="urlChange(addForm.method,'method')">
            <el-radio label="GET"></el-radio>
            <el-radio label="POST"></el-radio>
            <el-radio label="PUT"></el-radio>
            <el-radio label="DELETE"></el-radio>
          </el-radio-group>
        </el-form-item>
        <!-- <el-form-item label="图标" prop="icon">
                    <el-input auto-complete="off" v-model="addForm.icon"></el-input>
                </el-form-item> -->
        <el-form-item label="备注" class="source-form-textarea" prop="remark">
          <el-input auto-complete="off" v-model="addForm.remark" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('source_add2')">确 定</el-button>
        <el-button @click="addSourceVisible = false">取 消</el-button>
      </div>
    </da-dialog>

  </div>
</template>

<script>
const form = {
  name: "",
  uri: "",
  method: "GET",
  remark: ""
};
export default {
  name: "sourceManage",
  data() {
    let me = this;
    var valiUrl = function(rule, value, callback) {
        if (!value) {
          return callback(new Error("请输入资源地址"));
        } else {
          if (me.flag.changeUrl && me.flag.isOpen) {
            me.$ajax
              .get("rest/module/uriIsLegal", {
                params: {
                  uri: value,
                  method: me.addForm.method
                },
                noLoading: true
              })
              .then(res => {
                if (res.data.status) {
                  if (res.data.records) {
                    callback();
                  } else {
                    return callback(new Error("资源地址重复"));
                  }
                } else {
                  let msg = res.data.answerMsg
                    ? res.data.answerMsg
                    : "操作失败";
                  me.$message.error(msg);
                }
              });
          } else {
            callback();
          }
        }
      },
      valiType = function(rule, value, callback) {
        if (!value) {
          return callback(new Error("请输入资源类型"));
        } else {
          if (me.flag.changeUrl && me.flag.isOpen) {
            me.$ajax
              .get("rest/module/uriIsLegal", {
                params: {
                  uri: me.addForm.uri,
                  method: value
                },
                noLoading: true
              })
              .then(res => {
                me.$refs.source_add2.validateField("uri");
                if (res.data) {
                  callback();
                } else {
                  return callback(new Error("资源地址重复"));
                }
              });
          } else {
            callback();
          }
        }
      };
    return {
      columnData: [
        {
          label: "资源名称",
          prop: "name",
          align: "left",
          sortable: true
        },
        {
          label: "访问地址",
          prop: "uri",
          align: "left",
          sortable: true
        },
        {
          label: "请求类型",
          prop: "method",
          sortable: true
        },
        // {
        //   label: "图标",
        //   prop: "icon",
        //   align: "left",
        //   sortable: true
        // },
        {
          label: "备注",
          prop: "remark",
          align: "left",
          sortable: true
        }
      ],
      form: {
        currentPage: 1,
        pageSize: 10,
        formdata: {
          name: "",
          uri: "",
          remark: ""
        }
      },
      addForm: JSON.parse(JSON.stringify(form)),
      options: [
        { label: "GET", value: "GET" },
        { label: "POST", value: "POST" },
        { label: "PUT", value: "PUT" },
        { label: "DELETE", value: "DELETE" }
      ],
      selectData: [],
      rules: {
        name: [
          { required: true, message: "请输入资源名称", trigger: "blur" },
          { max: 50, message: "长度不能超过50个字符", trigger: "blur" }
        ],
        uri: [
          { required: true, message: "请输入资源地址", trigger: "blur" },
          { max: 200, message: "长度不能超过200个字符", trigger: "blur" },
          { validator: valiUrl, trigger: "blur" }
        ],
        // icon: [{ max: 20, message: "长度不能超过20个字符", trigger: "blur" }],
        remark: [
          { max: 100, message: "长度不能超过100个字符", trigger: "blur" }
        ],
        method: [{ validator: valiType, trigger: "change" }]
      },
      addSourceVisible: false,
      isAdd: false,
      textP: "",
      flag: {
        changeUrl: false, //url是否编辑
        isOpen: false
      },
      valiOnly: {
        uri: "",
        method: ""
      }
      //                flag:0,//判断radio的change事件是否触发
    };
  },
  methods: {
    //请求资源
    requestResource() {
      let me = this;
      me.$ajax
        .get("rest/module/baseResource/page", {
          params: me.form,
          loadingTarget: this.$refs.table
        })
        .then(res => {
          if (res.data.status) {
            me.$invokeSubMethod("setData", "table", res.data);
          }
        })
        .catch(err => {});
    },
    showAddDialog() {
      this.addSourceVisible = true;
      this.isAdd = true;
    },
    edit(val) {
      let me = this;
      this.addSourceVisible = true;
      this.isAdd = false;
      this.addForm = JSON.parse(JSON.stringify(val));
      this.valiOnly.uri = val.uri;
      this.valiOnly.method = val.method;
    },
    /*
            deleteSource(val) {
                let me = this;
                this.$alertMsg("确定删除所选的资源吗?", "操作确认", "warning", function () {
                    me.$ajax({
                        url: "rest/module/baseResource",
                        method: "delete",
                        data: val
                    }).then((res) => {
                        if (res.data.status) {
                            me.requestResource();
                            me.$message.success(res.data.answerMsg);
                        } else {
                            me.$message.error(res.data.answerMsg);
                        }
                    })
                });
            },
            */
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = JSON.parse(JSON.stringify(form));
        this.flag.changeUrl = false;
        this.flag.isOpen = false;
      });
    },
    saveDialog(formName) {
      let me = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (me.isAdd) {
            //发送新增请求

            this.$ajax
              .post("rest/module/baseResource", me.addForm)
              .then(res => {
                if (res.data.status) {
                  me.$message.success(res.data.answerMsg);
                  me.addSourceVisible = false;
                  this.requestResource();
                } else {
                  this.$message.error(res.data.answerMsg);
                }
              });
          } else {
            //发送编辑请求
            this.$ajax
              .put("rest/module/baseResource", me.addForm)
              .then(res => {
                if (res.data.status) {
                  me.$message.success(res.data.answerMsg);
                  me.addSourceVisible = false;
                  me.requestResource();
                } else {
                  this.$message.error(res.data.answerMsg);
                }
              })
              .catch(err => {});
          }
        } else {
          return false;
        }
      });
    },
    keydown(e) {
      if (e.keyCode == 32) {
        event.returnValue = false;
      }
    },
    urlChange(val, name) {
      if (name == "uri") {
        this.addForm.uri = val.replace(/\s+/g, ""); //'这是一个 测试'
      }

      if (val == this.valiOnly[name]) {
        this.flag.changeUrl = false;
      } else {
        this.flag.changeUrl = true;
      }
    },
    openDialog() {
      this.$nextTick(function() {
        this.flag.isOpen = true;
        this.flag.changeUrl = false;
      });
    },
    changeRadio(val) {
      this.flag.changeUrl = true;
    }
  },
  computed: {
    title() {
      return this.isAdd ? "新增资源" : "编辑资源";
    }
  },
  mounted() {
    this.requestResource();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only  -->
<style>
.source-div-addDialog2 .el-dialog {
  width: 500px;
}

.el-icon-my-xinzengziyuan {
  margin-right: -5px;
}
</style>



